module.exports = ".header {\n  height: 100px;\n  width: 100%;\n  position: fixed;\n  background-color: rgba(0, 0, 0, 0.8);\n  display: flex;\n  font-family: 'Julius Sans One', sans-serif;\n  z-index: 40;\n}\n.header .logo {\n  position: absolute;\n  height: 100px;\n  width: 100px;\n  background-image: url('milesAheadLogo.png');\n  background-size: cover;\n  outline: none;\n  z-index: 41;\n  top: 0;\n  left: 0;\n  transition: 0.3s;\n}\n@media screen and (min-width: 500px) {\n  .header .logo {\n    margin-left: 20px;\n    margin-right: 20px;\n  }\n}\n.header .inflate {\n  transform: scale(1.15);\n  top: 10px;\n  left: 10px;\n  transition: 0.3s;\n}\n@media screen and (min-width: 500px) {\n  .header .inflate {\n    top: 30px;\n    transform: scale(1.5);\n  }\n}\n@media screen and (min-width: 1200px) {\n  .header .inflate {\n    top: 100px;\n    margin-left: 100px;\n    transform: scale(2.5);\n  }\n}\n.header .navSection {\n  width: calc(100% - 140px);\n  margin-left: 100px;\n  padding-left: 20px;\n  padding-right: 20px;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  color: white;\n}\n@media screen and (max-width: 330px) {\n  .header .navSection {\n    font-size: 12px;\n  }\n}\n@media screen and (min-width: 500px) {\n  .header .navSection {\n    padding-left: 50px;\n    padding-right: 50px;\n    margin-left: 120px;\n    width: calc(100% - 240px);\n    font-size: 30px;\n  }\n}\n@media screen and (min-width: 1200px) {\n  .header .navSection {\n    width: calc(100% - 440px);\n    margin-left: 300px;\n  }\n}\n.header .navSection .navBtn {\n  text-align: center;\n  outline: none;\n  cursor: pointer;\n}\n@media screen and (min-width: 500px) {\n  .header .navSection .navBtn {\n    width: 120px;\n  }\n}\n@media screen and (min-width: 1200px) {\n  .header .navSection .navBtn {\n    width: 200px;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"